import React from 'react';
import { ranGenTot } from '../RandomGen';
import SkeletonCard from './SkeletonCard';

export default function Loading() {
    let fakeArr = new Array(5).fill(1);

    return (
        <>
            {
                fakeArr.map(() => {
                    return (<SkeletonCard key={ranGenTot()} />)
                })
            }
        </>
    )
}